.experience-section {
  /* padding-top: 7rem; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.experience-card {
  display: flex;
  margin: 3rem auto;
  max-width: 60rem;
  height: 16rem;
}

.card-image-container {
  margin: auto;
  margin-left: 2rem;
}

.card-description-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin: 2rem 2rem;
}

.card-image {
  display: block;
  width: 15rem;
  height: 10rem;
  object-fit: cover;
}

.card-title {
  margin: 0 1rem;
  text-transform: uppercase;
  letter-spacing: 0.4rem;
  font-weight: 300;
}

.card-description {
  font-weight: 300;
}

@media screen and (max-width: 980px) {
  .experience-card {
    max-width: 40rem;
  }

  .card-description-container {
    margin: 1rem 1rem;
  }

  .card-image {
    display: block;
    width: 15rem;
    height: 10rem;
    object-fit: cover;
  }

  .card-title {
    font-size: 1rem;
    margin: 1rem 2rem;
    letter-spacing: 0.3rem;
  }

  .card-description {
    margin: 1rem 1rem;
  }
}

@media screen and (max-width: 660px) {
  .experience-card {
    display: flex;
    flex-direction: column;
    margin: 2rem auto;
    max-width: 27rem;
    height: 32rem;
  }

  .card-image-container {
    margin: auto;
    margin-top: 2rem;
  }

  .card-image {
    margin-top: 2rem;
    width: 18rem;
    height: 12rem;
  }
}

@media screen and (max-width: 480px) {
  .experience-card {
    display: flex;
    flex-direction: column;
    margin: 2rem auto;
    max-width: 22rem;
    height: 27rem;
  }

  .card-image {
    width: 15rem;
    height: 8rem;
  }

  .card-title {
    font-size: 0.9rem;
    letter-spacing: 0.2;
  }

  .card-description {
    margin: 0 1rem;
    font-size: 0.8rem;
  }
}
